import React, { Component } from "react";
import Typography from "@material-ui/core/Typography";
import { withStyles } from "@material-ui/core/styles";
import { DateTimePicker, GradeInput } from "./CreateFields";
import Button from "@material-ui/core/Button";
import CourseSummary from "./CourseSummary";
import LoadingPage from "./LoadingPage";
import styles from "../../Assets/Quick_start_style";
import { Context } from "../../Context";
import SelectInput from "../Fields/SelectInput";
import TimeInput from "../Fields/TimeInput";
import JsonEditor from "../Fields/JsonEditor";
import TextInput from "../Fields/TextInput";
import RadioInput from "../Fields/RadioInput";
import CheckboxInput from "../Fields/CheckboxInput";
import TextAreaInput from "../Fields/TextAreaInput";
import UploadInput from "../Fields/UploadInput";
import TextEditor from "../Fields/TextEditorInput";
import MultipleUploadsInput from "../Fields/MultipleUploadsInput";
import CreateFieldWrapper from "../Fields/CreateFieldWrapper";
import SearchBar from "../Fields/SearchBar";

class ProgramWizard extends Component {
  constructor(props) {
    super(props);
    let state = {};
    window.props.createProgramSteps.forEach((step, index) => {
      let fields = step.map((fieldName, index) =>
        window.props.fields.find(el => el.name === fieldName)
      );
      state["step" + (index + 1)] = fields;
    });
    this.state = state;
  }

  createFields = (fields = [], blockedFields = []) => {
    const {
      handleFieldChange,
      handleSelectCourses,
      handleRemoveCourses,
      handleSearchBar,
      newProgram,
      courses,
      addProgramDetails
    } = this.context;
    const { classes } = this.props;
    return fields.map((field, key) => {
      switch (field.type) {
        case "text":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <TextInput
                field={field}
                defaultValue={newProgram[field.name] || ""}
                handleFieldChange={handleFieldChange}
                fixedField={blockedFields.includes(field.name)}
                newProgram={newProgram}
              />
            </CreateFieldWrapper>
          );
        case "select":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <SelectInput
                field={field}
                value={newProgram[field.name] || []}
                addCourseDetails={addProgramDetails}
                fixedField={blockedFields.includes(field.name)}
              />
            </CreateFieldWrapper>
          );
        case "searchBar":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <SearchBar
                field={field}
                defaultValue={newProgram[`${field.name}_bar`] || ""}
                handleSearchBar={handleSearchBar}
                handleSelectCourses={handleSelectCourses}
                handleRemoveCourses={handleRemoveCourses}
                fixedField={blockedFields.includes(field.name)}
                newProgram={newProgram}
                courses={courses}
              />
            </CreateFieldWrapper>
          );
        default:
          return "";
      }
    });
  };

  // IF ALL FIELDS ARE FILLED, YOU CAN GO TO NEXT PAGE
  validProgramParameters = (fields = []) => {
    let valid = true;
    const { newProgram } = this.context;
    fields.forEach(field => {
      if (field.required && !newProgram[field.name]) {
        valid = false;
      }
    });
    return valid;
  };

  render() {
    const {
      step,
      selectedCourse,
      translations,
      newProgram,
      toggleDialog,
      handleBackClick,
      launchProgramCreation,
      checkProgramId,
      editMode,
      blockedFields,
      deleteAsset
    } = this.context;
    const { classes } = this.props;
    const fields = this.state["step" + step];
    const disabledNext = !this.validProgramParameters(fields);

    return (
      <>
        <div className={classes.bandeau}>
          {[1, 2].includes(step) ? (
            <Typography>Step {step}/2</Typography>
          ) : (
            <h3 className={classes.bandeauTitle}>
              {translations.stepFields.courseSummary}
            </h3>
          )}
          <h3 className={classes.bandeauTitle}>Create Program - Mode</h3>
        </div>
        <div className={classes.createCourseWrapper}>
          {[1, 2].includes(step) &&
            this.createFields(
              fields,
              selectedCourse,
              blockedFields,
              editMode,
              deleteAsset
            )}
          {step === 5 && (
            <CourseSummary
              newProgram={newProgram}
              translations={translations}
              classes={classes}
              editMode={editMode}
            />
          )}
          {step === 6 && <LoadingPage />}
        </div>

        {[1, 2].includes(step) && (
          <div className={classes.createButtonsWrapper}>
            <span>
              {[2].includes(step) && (
                <Button
                  onClick={() => handleBackClick()}
                  className={classes.backButton}
                >
                  {translations["back"]}
                </Button>
              )}

              {step === 1 && (
                // PAGE 1
                <Button
                  disabled={disabledNext}
                  onClick={() => checkProgramId()}
                  className={disabledNext ? classes.disabledBtn : ""}
                  style={{ marginLeft: 10 }}
                >
                  {translations["next"]}
                </Button>
              )}
            </span>
            {step === 2 && (
              // PAGE 2
              <Button
                disabled={disabledNext}
                onClick={e => launchProgramCreation(e)}
              >
                {translations.createProgram || "Create Program"}
              </Button>
            )}
            <Button
              className={classes.cancelButton}
              onClick={() => toggleDialog()}
            >
              {translations["cancel"]}
            </Button>
          </div>
        )}
      </>
    );
  }
}
ProgramWizard.contextType = Context;
export default withStyles(styles)(ProgramWizard);
