import React, { Component } from "react";
import uid from "uid";
import { withStyles } from "@material-ui/styles";
import HeaderNav from "../Components/HeaderNav";
import { Provider } from "../Context";
import ProgramWizard from "../Components/CourseWizard/ProgramWizard";
import AlertDialog from "../Components/AlertDialog";
import styles from "../Assets/Quick_start_style.js";
import { NodeInfo } from "detect-browser";

const lmsBase =
  !process.env.NODE_ENV ||
  process.env.NODE_ENV === "development" ||
  window.props.lmsBase === "https://edx.devstack.lms:18000"
    ? "http://localhost:18010"
    : window.props.lmsBase;

const newProgramBasis = window.props && window.props.programBasis;
const translations = window.props && window.props.translations;

class NewProgramPage extends Component {
  state = {
    translations: window.props.translations,
    courses: window.props.courses || [],
    programs: window.props.programs || [],
    step: 2,
    selectedCourse: undefined,
    newProgram: {},
    cancelCreationPopup: false,
    newProgramResponse: undefined,
    blockedFields: []
  };

  // TO CHECK
  // componentWillMount() {
  //   const referredCourseId = this.props.match.params.referredCourseId;
  //   if (referredCourseId) {
  //     const selectedCourse = this.state.courses.find(
  //       course => course.course_id === referredCourseId
  //     );
  //     this.getcourseInfo(referredCourseId, selectedCourse);
  //     this.setState({
  //       mode: this.props.editMode === "configure" ? "configure" : "re-run",
  //       step: 1,
  //       blockedFields:
  //         this.props.editMode === "configure"
  //           ? ["org", "course_number", "course_session", "course_pacing"]
  //           : ["org"]
  //     });
  //   }
  // }

  // TO CHECK
  addProgramDetails = courseDetail => {
    this.setState(prevstate => ({
      newProgram: {
        ...prevstate.newProgram,
        [courseDetail.name]: courseDetail.value
      }
    }));
  };

  handleNextClick = () => {
    this.setState(prevState => ({
      step: prevState.step + 1
    }));
  };

  checkProgramId = async () => {
    let validId = false;
    if (process.env.NODE_ENV !== "development") {
      const { newProgram } = this.state;
      const url =
        window.props.api.programidChecking +
        "program-v1:" +
        newProgram.org.toString() +
        "+" +
        uid(10).toString();
      const settings = {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        }
      };
      console.log(url);
      const api_call = await fetch(url, settings);
      const data = await api_call.json();
      validId = data.details === "valid_new_id";
      this.setState(prevState => ({
        newProgram: { ...prevState.newProgram, validCourseId: validId }
      }));
      validId && this.handleNextClick();
    } else {
      // AVOID CHECKING ID FOR DEVELOPMENT MODE
      this.setState(prevState => ({
        newProgram: { ...prevState.newProgram, validCourseId: true }
      }));
      this.handleNextClick();
    }
  };

  // TO CHECK
  launchProgramCreation = async e => {
    e.preventDefault();
    this.handleNextClick();

    const url = window.props.api.programCreationUrl;
    // console.log(url);
    // console.log(this.getFormData());

    const settings = {
      method: "POST",
      body: this.getFormData(),
      contentType: false,
      processData: false
    };
    const api_call = await fetch(url, settings);
    const data = await api_call.json();
    if (data.code !== "404") {
      this.setState({ newProgramResponse: data });
    }
  };

  // TO CHECK
  getFormData = () => {
    let formData = new FormData();
    Object.entries(this.state.newProgram).forEach(courseInfo => {
      if (
        ["course_image", "teacher_image"].includes(courseInfo[0]) &&
        Array.isArray(courseInfo[1])
      ) {
        formData.append(courseInfo[0], this.state.newProgram[courseInfo[0]][0]);
      } else if (courseInfo[0] === "effort") {
        formData.append("effort", this.getCourseEffort());
      } else if (["course_type", "course_settings"].includes(courseInfo[0])) {
        const optionsList = window.props.fields.find(
          elem => elem.name === courseInfo[0]
        ).options;
        optionsList.map(option =>
          formData.append(option, courseInfo[1].includes(option))
        );
      } else if (courseInfo[0] === "course_map") {
        formData.append(
          "course_map",
          JSON.stringify(this.state.newProgram.course_map)
        );
      } else if (courseInfo[0] === "course_downloads") {
        let filesTitles = [];
        this.state.newProgram.course_downloads.forEach((element, key) => {
          formData.append("course_downloads" + key, element.file);
          filesTitles.push(element.title);
        });
        formData.append("download_files_titles", JSON.stringify(filesTitles));
      } else if (courseInfo[0] === "actual_course_downloads") {
        formData.append(
          "actual_course_downloads",
          JSON.stringify(courseInfo[1])
        );
      } else {
        formData.append(courseInfo[0], courseInfo[1]);
      }
    });
    formData.append("editMode", this.props.editMode);
    return formData;
  };

  handleBackClick = () => {
    this.setState(prevState => ({
      step: prevState.step - 1
    }));
  };

  handleFieldChange = (fieldName, fieldValue) => {
    if (["course_session", "course_number"].includes(fieldName)) {
      fieldValue = fieldValue
        .replace(/ /g, "")
        .replace(/[^a-zA-Z0-9\-]/g, "")
        .toLowerCase();
    }
    this.setState(prevState => {
      return {
        newProgram: {
          ...prevState.newProgram,
          [fieldName]: fieldValue,
          validCourseId: true
        }
      };
    });
  };

  handleSearchBar = (fieldName, fieldValue) => {
    let coursesResult = window.props.courses.filter(course =>
      course.course_id.includes(fieldValue)
    );

    this.setState(prevState => {
      return {
        newProgram: {
          ...prevState.newProgram,
          [fieldName]: fieldValue,
          validCourseId: true
        },
        courses: coursesResult
      };
    });
  };

  handleSelectCourses = (fieldName, course) => {
    // CREATE EMPTY ARRAY IF NOT CREATED
    let coursesArray = [];
    if (!this.state.newProgram[fieldName]) {
      this.setState(prevState => {
        return {
          newProgram: {
            ...prevState.newProgram,
            [fieldName]: [],
            [`${fieldName}_bar`]: ""
          }
        };
      });
    }

    // MAKE SURE IT ITERATES ONLY WHEN ARRAY IS NOT EMPTY
    if (
      this.state.newProgram[fieldName] &&
      this.state.newProgram[fieldName].length > 0
    ) {
      coursesArray = [...this.state.newProgram[fieldName]];
    }
    if (!coursesArray.includes(course)) {
      coursesArray.push(course);
    }

    this.setState(prevState => {
      return {
        newProgram: {
          ...prevState.newProgram,
          [fieldName]: coursesArray,
          [`${fieldName}_bar`]: ""
        }
      };
    });
  };

  handleRemoveCourses = (fieldName, courseId) => {
    let coursesArray = [...this.state.newProgram[fieldName]];
    console.log(coursesArray.indexOf(courseId));
    if (coursesArray.includes(courseId)) {
      let index = coursesArray.indexOf(courseId);
      coursesArray.splice(index, 1);
    }
    this.setState(prevState => {
      return {
        newProgram: {
          ...prevState.newProgram,
          [fieldName]: coursesArray
        }
      };
    });
  };

  toggleDialog = () => {
    this.setState(prevstate => ({
      cancelCreationPopup: !prevstate.cancelCreationPopup
    }));
  };

  eraseNewProgram = () => {
    this.setState(
      {
        newProgram: newProgramBasis,
        step: 1,
        cancelCreationPopup: false,
        selectedCourses: []
      },
      (window.location.href = "/")
    );
  };

  getContext = () => {
    return {
      ...this.state,
      handleSearchBar: this.handleSearchBar,
      handleFieldChange: this.handleFieldChange,
      handleNextClick: this.handleNextClick,
      addProgramDetails: this.addProgramDetails,
      toggleDialog: this.toggleDialog,
      handleBackClick: this.handleBackClick,
      launchProgramCreation: this.launchProgramCreation,
      checkProgramId: this.checkProgramId,
      eraseNewProgram: this.eraseNewProgram,
      editMode: this.props.editMode,
      blockedFields: this.state.blockedFields,
      deleteAsset: this.deleteAsset,
      handleSelectCourses: this.handleSelectCourses,
      handleRemoveCourses: this.handleRemoveCourses
    };
  };

  render() {
    const { classes } = this.props;
    // const { step, mode, selectedCourse } = this.state;
    // const courses = this.state.courses.filter(
    //   course =>
    //     course.type !== "vodeclic" && window.props.orgs.includes(course.org)
    // );
    return (
      <>
        <Provider value={this.getContext()}>
          <HeaderNav />
          <div className={classes.createPageWrapper}>
            <ProgramWizard />
          </div>
          <AlertDialog
            openDialog={this.state.cancelCreationPopup}
            toggleDialog={this.toggleDialog}
            dialogTitle={translations.cancelProgramTitle}
            dialogContent={translations.cancelProgramContent}
            dialogValid={translations.validCancelCreation}
            dialogCancel={translations.backToProgram}
            dialogAction={this.eraseNewProgram}
          />
        </Provider>
      </>
    );
  }
}

export default withStyles(styles)(NewProgramPage);
