import React from "react";
import styles from "../../Assets/Quick_start_style.js";
import { withStyles } from "@material-ui/core";
import FormGroup from "@material-ui/core/FormGroup";
import Checkbox from "@material-ui/core/Checkbox";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";

const translations = window.props && window.props.translations;

const CheckBoxInput = ({ field, classes, addCourseDetails, value }) => {
  const handleChange = name => event => {
    event.target.checked
      ? addCourseDetails({
          name: field.name,
          value: [...value[field.name], event.target.value]
        })
      : addCourseDetails({
          name: field.name,
          value: [...value[field.name]].filter(
            elem => elem != event.target.value
          )
        });
  };
  return (
    <FormControl component="fieldset">
      <FormGroup>
        {field.options.map((option, key) => (
          <div key={key}>
            <FormControlLabel
              control={
                <Checkbox
                  value={option}
                  onChange={handleChange(option)}
                  checked={value[field.name].includes(option)}
                />
              }
              label={translations.stepFields[option + "_title"] || option}
            />
            <FormHelperText>
              {translations.stepFields[option + "_helper"]}
            </FormHelperText>
          </div>
        ))}
      </FormGroup>
    </FormControl>
  );
};

export default withStyles(styles)(CheckBoxInput);
